<template>
  <div class="card">
    <el-row :gutter="10">
      <!-- 左标题 -->
      <el-col class="tL" :span="13">
        <span @click="titleChange($event)">{{ title }}</span>
      </el-col>
      <!-- 右标题 -->
      <el-col class="tR" :span="11">
        <div class="cell"><span>倒</span></div>
        <div class="cell"><span>计</span></div>
        <div class="cell"><span>时</span></div>
      </el-col>
    </el-row>
    <el-row>
      <!-- 天数 -->
      <el-col>
        <span @click="show" class="day">{{ days }} 天</span>
      </el-col>
    </el-row>
    <!-- 时分秒 -->
    <el-col class="clock">{{ hours }}时 {{ mins }}分 {{ secs }}秒</el-col>
    <!-- 日期选择框 -->
    <div class="block" v-show="showDate">
      <el-date-picker
        v-model="dateTime"
        type="date"
        @change="changeDate($event)"
        placehLengther="请选择终止的日期"
      >
      </el-date-picker>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: "高考",
      dateTime: "",
      showDate: false,
      days: 0,
      hours: 0,
      mins: 0,
      secs: 0,
    };
  },

  created: function() {
    // 提示框
    this.$message.success({
      message: "欢迎进入：倒 计 时 ！",
    });

    // 初始项目：高考
    this.GaoKao();

    // 倒计时开始。
    setInterval(() => {
      this.countDown();
    }, 1000);
  },

  methods: {
    // ** 倒计时总模块 **
    countDown() {
      // 获取倒计时的时间长度：天、时、分、秒。 timetimeDuration 时间长短。
      let timeLength;

      // 每秒获取一次时间，形成计时器。 window.EndLine 是截止日期。
      let today = new Date();
      timeLength = window.EndLine.getTime() - today.getTime();
      // console.log(timeLength);

      let msPerDay = 24 * 60 * 60 * 1000; //每天的毫秒数
      let e_daysold = timeLength / msPerDay;
      let daysold = Math.floor(e_daysold); //天数
      let e_hrsold = (e_daysold - daysold) * 24;
      let hrsold = Math.floor(e_hrsold); //时数
      let e_minsold = (e_hrsold - hrsold) * 60;
      let minsold = Math.floor((e_hrsold - hrsold) * 60); //分数
      let seconds = Math.floor((e_minsold - minsold) * 60); //秒数
      // console.log(
      //   "还有" +
      //     daysold +
      //     "天" +
      //     hrsold +
      //     "小时" +
      //     minsold +
      //     "分" +
      //     seconds +
      //     "秒" +
      //     "！"
      // );
      this.days = daysold;
      this.hours = hrsold;
      this.mins = minsold;
      this.secs = seconds;
    },

    // ** 年份更新：根据“月日”2个参数进行年份校验 **
    updateYear(month, day) {
      // 1、获取当前日期：年、月、日。
      let today = new Date();
      var strYear = today.getFullYear();
      var strMonth = today.getMonth() + 1;
      var strDate = today.getDate();
      // 2、判断是否超过今年的截止日：month月 day日。
      let EndYear;
      if (strMonth >= month) {
        if (strDate >= day) {
          // 超过：则年份加1，开始计算下一年的倒计时。
          EndYear = strYear + 1;
        }
      } else {
        // 不超过：则年份不变，正常计算今年的倒计时。
        EndYear = strYear;
      }
      // 3、得到最新的截止日。
      window.updateYear = EndYear;
    },

    // ** 高考：日期校验 **
    GaoKao() {
      this.updateYear(6, 7);
      window.gaokaoDay = new Date("6-8-" + window.updateYear); // 计算倒计时，需增加一天计算。
      window.EndLine = window.gaokaoDay;
    },

    // ** 元旦节：日期校验 **
    YuanDan() {
      this.updateYear(1, 1);
      window.YuanDanDay = new Date("1-2-" + window.updateYear); // 计算倒计时，需增加一天计算。
      window.EndLine = window.YuanDanDay;
    },

    // ** 劳动节：日期校验 **
    LaoDong() {
      this.updateYear(5, 2);
      window.LaoDongDay = new Date("5-2-" + window.updateYear); // 计算倒计时，需增加一天计算。
      window.EndLine = window.LaoDongDay;
    },

    // ** 国庆节：日期校验 **
    GuoQing() {
      this.updateYear(10, 1);
      window.GuoQingDay = new Date("10-2-" + window.updateYear); // 计算倒计时，需增加一天计算。
      window.EndLine = window.GuoQingDay;
    },

    // 更改左标题
    titleChange(event) {
      // console.log(event);
      // 获取点击的文本
      let e = event.currentTarget;
      console.log(e.innerHTML);
      if (e.innerHTML == "高考") {
        this.title = "元旦";
        this.YuanDan();
      } else if (e.innerHTML == "元旦") {
        this.title = "劳动";
        this.LaoDong();
      } else if (e.innerHTML == "劳动") {
        this.title = "国庆";
        this.GuoQing();
      } else if (e.innerHTML == "国庆") {
        this.title = "高考";
        this.GaoKao();
      }
    },

    // ** 日期选择框 **
    show() {
      // 提示框
      this.showDate = !this.showDate;
      if (this.showDate == true) {
        this.$message({
          showClose: true,
          message: "可重新选择日期~",
        });
      }
    },

    // ** 日期更改 **
    changeDate(value) {
      this.$message.success({
        showClose: true,
        message: "日期选择成功~",
      });
      // 日期选择后，自动隐藏选项。
      this.showDate = !this.showDate;
      // 选择新日期后，替换掉初始日期。
      window.EndLine = value;
    },
  },
};
</script>

<style>
/* 左标题 */
.tL {
  font-size: 4rem;
  text-align: right;
  line-height: 4rem;
  font-family: "HYXS";
}
/* 右标题 */
.tR {
  text-align: left;
  margin-top: 1rem;
  font-size: 1.3rem;
  font-weight: bLength;
  line-height: 2.5rem;
  font-family: "楷体", "楷体_GB2312";
}
.cell {
  /* 圆形之间的距离 */
  margin: -5px;
}
.cell span {
  /* 圆形边框 */
  padding: 3px 4px 3px 3px;
  color: brown;
  border-radius: 50%;
  border: brown 1px solid;
}
/* 天数 */
.day {
  cursor: pointer;
  font-size: 2.5rem;
  font-family: "HYXS";
  margin-left: -10px;
}
/* 时分秒 */
.clock {
  letter-spacing: 1px;
  margin-left: -5px;
  opacity: 0.3;
}
/* 选择日期 */
.block {
  cursor: pointer;
  position: absolute;
  left: -30px;
}
.el-input__inner {
  cursor: pointer;
}
</style>
